.popup-bg, .popup-bg-image {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  height: 1050px;
  width: 100%;
}

.popup-bg {
  z-index: 2;
  background: rgba(102, 102, 102, 0.7);
}

.popup-bg-image {
  z-index: 1;
  background: url('../img/poly-map.jpg') center center;
  background-size: cover;
}

.popup-container {
  margin-top: 125px;

  .popup {
    padding: 0;
    z-index: 3;
    border: 1px solid rgb(147, 147, 147);
    border-radius: 5px;
    background: rgb(244, 248, 255);

    .popup-header {
      border-bottom: 1px solid rgb(207, 207, 207);
      text-align: left;
      background: #fff;

      .popup-header-inner {
        padding: 20px 20px;
      }

      h2 {
        margin: 0;
        padding: 0;
        font-size: 1.5em;
        color: rgb(126, 126, 126);
        font-weight: 200;
        display: inline-block;
      }

      a {
        color: rgb(207, 207, 207);
        font-size: 1.5em;
        float: right;
        margin: -5px 0 0 0;
        &:hover {
          text-decoration: none;
        }
      }
    }

    .popup-body {
      .popup-body-inner {
        padding: 35px 20px;
      }
      button {
        float: right;
  		margin-right: 17px;
  		margin: 75px 17px -15px auto;
      }
    }
  }
}

.popup-body {
  label {
    color: rgb(176, 176, 176);
    font-weight: 300;
  }
  .label-horz {
      margin-top: -15px;
      padding-bottom: 7px;
  }
  div.popup-sizeable-select-list select {
    background: white;
    -webkit-appearance:none;
       -moz-appearance: none;
            appearance: none;
    &:hover {
      cursor: pointer;
    }
  }
  .select-caret {
	position: relative;
	left: 85%;
	top: -17;
  }
}

.locations-form-group {
    margin-top: 35px;
    margin-bottom: 25px;
    .list-group {
        margin-top: 19px;
    }
}

#add-location {
    font-weight: 900;
    &:hover {
        cursor: pointer;
        background: rgb(222, 222, 222);
    }
}

#locations-list {
    list-style-type: none;
    padding: 0;
    margin: 0;
    margin: 5px auto;
    li.address, li.comma-seperator {
        display: inline-block;
        color: rgb(116, 116, 116);
        font-weight: 300;
        font-size: 0.75em;
    }
    li.address {
        padding: 2px 4px;
        border-radius: 5%;
        &:hover {
            background: rgb(237, 237, 237);
        }
    }
    li.comma-seperator {
        margin: auto 4px auto -2px;
    }
}
